<template>

  <button v-on:click="connectionWs()">Connect</button>


  <Login/>
  <Game/>
  <Chat/>

</template>

<script>
import Game from './components/Game.vue'
import Chat from './components/Chat.vue'
import Login from './components/Login.vue'



export default {
  name: 'App',
  components: {
    Game,
    Chat,
    Login
  },
  data: function() {
    return{
      connection: null
    }
  },

  methods: {
    connectionWs: function() {
      console.log("Starting connection to WebSocket Server")

      this.$store.state.connection = new WebSocket("ws://" + this.$store.state.host + ":8000/ws/" + this.$store.state.room)

      this.$store.state.connection.onopen = function(event) {
        console.log(event);
        console.log("Successfully connected to  server");
      }

      this.$store.state.connection.onmessage = (event) => {
        console.log("From Server: ", event);
        const data = JSON.parse(event.data)
        console.log("Data: ", data);

        if (data.action == 'create_ship' && data.result) {
          this.$store.state.battlefield = data.result;
        }

        if (data.action == 'restore' && data.result) {
          this.$store.state.battlefield = data.result[0];
          this.$store.state.battlefield2 = data.result[1];
        }


        if (data.action == 'fire') {
          if (data.username ==  this.$store.state.username) {
            this.$store.state.battlefield2[data.y][data.x] = data.result;
            console.log('Attack!')
          } else {
            this.$store.state.battlefield[data.y][data.x] = data.result;
            console.log('Attack us!')
          }
        }




        if (data.message) {
          this.$store.state.history.push(data);
        }

        if (data.history) {
          this.$store.state.history = data.history;
        }


      },
      this.$store.state.connection.close  = function() {
        this.$store.state.connection = null;
      }

    }
  },
   created: function () {
    this.connectionWs();
    console.log('createAPP!');
  }
}
</script>

