import { createStore } from 'vuex'

const battlefield = [];
for (let i = 0; i<10; i++) {
  const battleRow = [];
  for (let j = 0; j<10; j++) {
    battleRow.push(0);
  }
  battlefield.push(battleRow);
}

const room = window.location.pathname.replace('/room/', '');
const host = window.location.hostname

console.log(battlefield)


export default createStore({
  state: {
    host,
    room,

    username: '',
    battlefield,
    battlefield2: battlefield,

    history: [],
    message: '',
    connection: null,
    direction: false
  },
  mutations: {
    updateBattlefield (state, battlefield) {
      state.battlefield = battlefield;
    }
  },
  actions: {
  },
  modules: {
  },
  getters: {
    getBattlefield: state => {
      return state.battlefield;
    },
    getBattlefield2: state => {
      return state.battlefield2;
    }
  }
})
