<template>

  <div class="chat-wrap">
    <div class="chat-history">
      <div v-for="message in this.$store.state.history" :key="message">
        <div class="message">
          <span>От {{message.username}}</span><br>
          <span>{{message.message}}</span>
        </div>
      </div>
    </div>

    <input v-model="this.$store.state.message" placeholder="Message">
    <button v-on:click="sendMessage()">Send</button>
    <button v-on:click="loadHistory()">Load history</button>
  </div>


</template>

<script>


export default {

  methods: {
    sendMessage: function() {
      this.$store.state.connection.send(JSON.stringify({
                                        'type': 'chat_message',
                                        'data': {
                                          'username': this.$store.state.username,
                                          'message': this.$store.state.message
                                          }
                                        }));
      this.$store.state.message = '';
    },
    loadHistory: function() {
      this.$store.state.connection.send(JSON.stringify({
                                        'type': 'get_history',
                                        'data': {}
                                      }));
    }
  }
}
</script>

<style>

.chat-wrap{
  max-width: 800px;
}

.chat-history{
  height: 400px;
  overflow-y: scroll;
}

.message{
  background-color: #ddd;
  max-width: 600px;
  margin: 7px;
  padding: 7px;
}

</style>
