<template>
  <h1>Seabattle</h1>
    <div class="control">
      <span>Горизонтальный корабль</span>
      <input type="checkbox" value="test" v-model=this.$store.state.direction>
      <button v-on:click="restoreGame()">Restore Game</button>
    </div>
    <hr>
    
    <div class="battlefilds">
      <div class="my-battlefield">
        <div class="battlefield-wrap">
          <div v-for="(row, row_index) in getBattlefield" :key="row">
            <div class="row">
              <div v-for="(place, index) in row" :key="place">
                <div class="element" v-on:click="createShip(row_index, index)"  v-bind:id="(row_index*10+index)">
                  <div v-if="place === 0">
                    <div class="place"></div>
                  </div>
                  <div v-else-if="place === 1">
                    <img src="@/assets/ship.svg" alt="">
                  </div>
                  <div v-else-if="place === 2">
                    <img src="@/assets/miss.svg" alt="">
                  </div>
                  <div v-else-if="place === 3">
                    <img src="@/assets/dead.svg" alt="">
                  </div>
              </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="opponent-battlefield">
        <div class="battlefield-wrap">
          <div v-for="(row, row_index) in getBattlefield2" :key="row">
            <div class="row">
              <div v-for="(place, index) in row" :key="place">
                <div class="element" v-on:click="fire(row_index, index)"  v-bind:id="(row_index*10+index)">
                  <div v-if="place === 0">
                    <div class="place"></div>
                  </div>
                  <div v-else-if="place === 1">
                    <img src="@/assets/ship.svg" alt="">
                  </div>
                  <div v-else-if="place === 2">
                    <img src="@/assets/miss.svg" alt="">
                  </div>
                  <div v-else-if="place === 3">
                    <img src="@/assets/dead.svg" alt="">
                  </div>
              </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>




</template>

<script>

import { mapGetters } from 'vuex';



export default {
  components: {
  },
  computed: {
    ...mapGetters(['getBattlefield', 'getBattlefield2'])
  },
  methods: {

    createShip: function (y_index, x_index) {
      this.$store.state.connection.send(
        JSON.stringify({
          'type': 'game',
          'data': {
            'action': 'create_ship',
            'username': this.$store.state.username,
            'x': x_index,
            'y': y_index,
            'direction': this.$store.state.direction
          }
        })
      );
    },
    fire: function (y_index, x_index) {
      console.log('Fire: ', y_index, x_index)
      this.$store.state.connection.send(
        JSON.stringify({
          'type': 'game',
          'data': {
            'action': 'fire',
            'username': this.$store.state.username,
            'x': x_index,
            'y': y_index,
            'direction': this.$store.state.direction
          }
        })
      );
    },
    restoreGame: function () {
      this.$store.state.connection.send(
        JSON.stringify({
          'type': 'restore',
          'data': {
            'username': this.$store.state.username,
            'action': 'restore'}
        })
      );
      console.log('Restore game')
    },
  }
}
</script>

<style>

.battlefilds{
  display: flex;
}

.opponent-battlefield{
  margin-left: 24px;
}

.row{
  display: flex;
}

.element{
  width: 32px;
  height: 32px;
  border: 2px solid;

}

.element>div{
  display: flex;
  align-items: center;
  justify-content: space-around;
}
</style>
